import {useState} from "react";
import "./LoginPanel.css"
import $ from 'jquery';

export default function LoginPanel({ handleLogin }) {

    return (
        <div id="loginPanel">
            <div id="loginBox">
                <p id="loginTitle">Logowanie do SUS</p>
                <input className="loginInput" type="text" placeholder="Nazwa użytkownika" id="nazwa"/><br/>
                <input className="loginInput" type="password" placeholder="Hasło"/><br/>
                <button id="loginButton" onClick={()=>{
                    handleLogin($('#nazwa').val());
                }}>Zaloguj</button>
            </div>
        </div>
    )
}